
import Vue, { PropType } from "vue"

export default Vue.extend({
  props: {
    // String
    href: {
      type: [] as PropType<any>,
      required: false,
      default: null,
    },
    icon: {
      type: String,
      required: false,
      default: null,
    },

    // Boolean
    block: {
      type: Boolean,
      required: false,
      default: false,
    },
    tight: {
      type: Boolean,
      required: false,
      default: false,
    },
    elevated: {
      type: Boolean,
      required: false,
      default: false,
    },
    variant: {
      type: String as PropType<"primary" | "secondary" | "github">,
      required: false,
      default: "primary",
    },
    rounded: {
      type: Boolean,
      required: false,
      default: false,
    },
    blank: {
      type: Boolean,
      required: false,
      default: false,
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  computed: {
    getIconName(): string {
      return this.icon?.startsWith("Icon") ? this.icon : `Icon${this.icon}`
    },
    getVariantClasses(): string {
      if (this.variant === "github")
        return "bg-gray-200 text-black bg-opacity-100 dark:bg-slate-800 dark:text-white  hover:bg-opacity-60"
      return "text-gray-700 dark:text-slate-400"
    },
  },
})
