
import Vue from "vue"

export default Vue.extend({
  computed: {
    /**
     * Return target url.
     */
    getTargetLink(): string {
      const path = this.$route.path

      if (path !== "/projects/Retrieve-Github-Commits" && path.startsWith("/projects/Retrieve-Github-Commits"))
        return "/projects/Retrieve-Github-Commits"
      else return "/"
    },
  },
})
