
import Vue from "vue"

interface ICategory {
  id: string
  title: string
}

interface IPage {
  title: string
  href: string
  icon: string
  iconProps?: {
    [key: string]: string
  }
  category: string
}

export default Vue.extend({
  data() {
    return {
      search: "",
      categories: [
        { id: "pages", title: "Pages" },
        { id: "me", title: "Me" },
        { id: "ql", title: "Quick Links" },
      ] as ICategory[],
      pages: [
        /* Pages */
        {
          title: "Home",
          href: "/",
          icon: "IconHome",
          category: "pages",
        },
        {
          title: "Blog",
          href: "/blog",
          icon: "IconDocument",
          category: "pages",
        },
        {
          title: "Donate",
          href: "/donate",
          icon: "IconDollar",
          category: "pages",
        },
        {
          title: "Daily Song",
          href: "/daily",
          icon: "IconMusicNote",
          category: "pages",
        },

        /* Me */
        {
          title: "Repositories",
          href: "/me/repos",
          icon: "IconBranch",
          category: "me",
        },
        {
          title: "Songs",
          href: "/me/songs",
          icon: "IconStar",
          category: "me",
        },
        {
          title: "Contact",
          href: "/me/contact",
          icon: "IconInbox",
          category: "me",
        },

        /* Quick Links */
        {
          title: "GitHub",
          href: "https://github.com/codemikko",
          icon: "IconBrand",
          iconProps: {
            brand: "github",
          },
          category: "ql",
        },
        {
          title: "Twitter",
          href: "https://twitter.com/codemikko",
          icon: "IconBrand",
          iconProps: {
            brand: "twitter",
          },
          category: "ql",
        },
      ] as IPage[],
    }
  },

  computed: {
    isPaletteVisible() {
      return this.$store.state?.palette?.visible || false
    },

    getCategoriesFiltered(): { category: ICategory; pages: IPage[] }[] {
      const categories = this.categories
      const items: { category: ICategory; pages: IPage[] }[] = []

      for (const category of categories) {
        const categoryItems = this.pages.filter(
          (page) =>
            page.category === category.id &&
            (this.search
              ? page.title?.toLowerCase().includes(this.search?.toLowerCase())
              : true)
        )

        if (categoryItems.length > 0) {
          items.push({ category, pages: categoryItems })
        }
      }

      return items
    },
  },

  /*
      Watch for changes to the isVisible and disable scrolling
      when the palette is visible.
    */
  watch: {
    "$route.fullPath"() {
      if (this.isPaletteVisible) {
        this.$store.commit("palette/toggleVisibility", false)
      }
    },

    isPaletteVisible(isVisible: boolean) {
      const keys = ["overflow-y-hidden"]

      if (isVisible) document.body.classList.add(...keys)
      else document.body.classList.remove(...keys)
    },
  },

  mounted() {
    window.addEventListener("keydown", this.handleKeyDown)
  },

  beforeDestroy() {
    window.removeEventListener("keydown", this.handleKeyDown)
  },

  methods: {
    toggleVisibility(value?: boolean) {
      this.$store.commit("palette/toggleVisibility", value)
    },

    handleKeyDown(event: KeyboardEvent) {
      if (
        (event.metaKey || event.ctrlKey) &&
        event.key === "k" &&
        !event.shiftKey &&
        !event.altKey
      ) {
        event.preventDefault()
        this.toggleVisibility()
      } else if (event.key === "Escape") {
        this.toggleVisibility(false)
      } else {
        this.$refs.searchInput?.focus()
      }
    },

    changeColorMode() {
      this.$colorMode.preference =
        this.$colorMode.value === "dark" ? "light" : "dark"
    },
  },
})
