
import Vue from "vue"

export default Vue.extend({
  head() {
    let string = "HelloMikko"
    if (this.routeIsBlog) string = "HelloMikko - blog"

    return {
      titleTemplate: `%s - ${string}`,
      htmlAttrs: {
        class: `min-h-screen ${this.$colorMode.value}`,
      },
      meta: [
        {
          hid: "og:site_name",
          name: "og:site_name",
          content: string,
        },
      ],
    }
  },
  computed: {
    /**
     * Checks if current route is a blog route.
     * @returns {boolean | undefined}
     */
    routeIsBlog(): boolean | undefined {
      return this.$route.name?.includes("blog")
    },
  },
})
