
import Vue, { PropType } from "vue"

export default Vue.extend({
  name: "SmartLink",
  props: {
    href: {
      type: [] as PropType<any>,
      required: true,
      default: "",
    },
    blank: {
      type: Boolean,
      required: false,
      default: false,
    },
    internal: {
      type: Boolean,
      required: false,
      default: false,
    },
    external: {
      type: Boolean,
      required: false,
      default: false,
    },
    title: {
      type: String,
      required: false,
      default: null,
    },
    utm: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  computed: {
    /**
     * Checks if the href leads to an internal page and returns boolean.
     * @returns {boolean}
     */
    isInternalPage(): boolean {
      const href = this.href

      if (typeof href === "object") return true
      else if (typeof href === "string" && ["/", "#"].includes(href[0]))
        return true
      else return false
    },
    /**
     * Adds UTM query parameter to URL and returns it.
     * @returns {string}
     */
    getUtmLink(): string {
      try {
        const url = new URL(this.href)
        url.searchParams.append("utm_source", "mikko.codes")

        return url.href
      } catch (err) {
        return this.href
      }
    },
  },
})
